<template>
  <div class="sms-wrapper wrapper-container">
    <p class="title"></p>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="120px"
      class="demo-ruleForm"
    >
      <!--  -->
      <el-form-item label="" prop="taskName">
        <el-row>
          <el-col :span="8">
            <el-input v-model="ruleForm.taskName" placeholder="30" clearable></el-input>
          </el-col>
        </el-row>
      </el-form-item>

      <!--  -->
      <el-form-item label="" prop="signId">
        <el-row>
          <el-col :span="4">
            <el-select v-model="ruleForm.signId" placeholder="" clearable>
              <el-option
                v-for="(item,index) in searchSigns"
                :key="index"
                :label="item.sign"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-col>
        </el-row>
      </el-form-item>

      <el-row>
        <el-col :span="12">
          <p class="signle-title">
            * 
            <router-link to="/smsCentre/signature">
              <span class="tag-title"></span>
            </router-link>
          </p>
        </el-col>
      </el-row>

      <!--  -->
      <el-form-item label="">
        <el-row>
          <el-col :span="4">
            <el-select
              v-model="ruleForm.smsTemplates1"
              placeholder=" "
              @change="chooseTemplate"
            >
              <el-option
                v-for="(item,index) in options"
                :label="item.label"
                :value="item.value"
                :key="index"
              ></el-option>
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-form-item prop="templateId">
              <el-select
                v-model="ruleForm.templateId"
                filterable
                placeholder=""
                clearable
                @change="chooseModelChild"
              >
                <el-option
                  v-for="(item,index) in templateDatas"
                  :key="index"
                  :label="item.templateName"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>
      <el-row>
        <el-col :span="24">
          <p class="signle-title">
            * 
            <router-link to="/smsCentre/stencil">
              <span class="tag-title"></span>
            </router-link>
          </p>
        </el-col>
      </el-row>

      <!--  -->

      <el-form-item label="">
        <el-row>
          <el-col :span="12">
            <el-radio-group v-model="radio" @change="chooseTelModel">
              <el-radio label="1" :disabled="variableSign == 1"></el-radio>
              <el-radio label="2"></el-radio>
            </el-radio-group>
          </el-col>
        </el-row>
      </el-form-item>

      <el-form-item v-if="radio == '1'">
        <el-row>
          <el-col :span="3">
            <el-form-item prop="notVariableMobile">
              <el-input
                v-model="ruleForm.notVariableMobile"
                maxlength="11"
                placeholder=""
                clearable
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>

      <!-- excel -->
      <div v-if="radio == '2'" class="excel-content">
        <!-- excel  -->
        <el-row>
          <el-col :span="8">
            <el-tooltip
              class="item"
              effect="light"
              popper-class="tel-tooltip"
              :content="telContent"
              placement="top"
              v-show="telContent"
            >
              <div slot="content" class="tel-box">{{telContent}}</div>

              <div class="tel-content">{{telContent}}</div>
            </el-tooltip>
          </el-col>
        </el-row>
        <span class="tel-task" v-show="telContent">20</span>
        <el-row class="tel">
          <el-col :span="4">
            <!--  -->
            <MyUpload
              @handleGetFile="handleGetFile"
              @handleExcelData="handleExcelData"
              ref="uploadRef"
            ></MyUpload>
          </el-col>
        </el-row>
        <ul class="task-title">
          <li>
            xlsx
            <span class="down-btn" @click="handleTelTemplate"></span>
            <el-tooltip class="item" effect="light" placement="top" popper-class="tip-task">
              <div slot="content" class="tip-task">
                :'${param}${param}
                <br />APP'
              </div>
              <span class="model-btn"></span>
            </el-tooltip>
          </li>
          <li>Excel</li>
          <li></li>
          <li>3M50</li>
        </ul>
      </div>

      <!--  -->

      <el-form-item label="" prop="sendSign">
        <el-switch v-model="sendSign" @change="handleSwitch"></el-switch>
      </el-form-item>

      <!--  -->
      <el-form-item label="" v-if="sendSign" required>
        <el-row>
          <el-col :span="4">
            <el-form-item prop="sendDt1">
              <el-date-picker
                v-model="ruleForm.sendDt1"
                type="date"
                placeholder=""
                :picker-options="pickerOptions"
                value-format="yyyy/MM/dd"
                @change="handleDateTime"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="4">
            <el-form-item prop="sendDt2">
              <el-time-picker
                arrow-control
                placeholder=""
                v-model="ruleForm.sendDt2"
                value-format="HH:mm:ss"
                @change="handleDateTime2"
              ></el-time-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-row v-if="isShowDate">
          <el-col :span="24">
            <div class="date-task">!</div>
          </el-col>
        </el-row>
      </el-form-item>

      <!--  -->
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        <!-- <el-button @click="submitForm('ruleForm','test')"></el-button> -->
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { SmsService } from "@/service"; // 
import sessionStorage from "./../../../utils/sessionStorage.js";
import { Validate } from "@/utils"; // 
import MyUpload from "./myUpload";
import { Axios } from "@/utils";

//
const options = [
  { value: "", label: "" },
  { value: "1", label: "" },
  { value: "2", label: "" },
  { value: "3", label: "" }
];

export default {
  name: "sms",
  components: { MyUpload },
  data() {
    return {
      pickerOptions: {
        // 
        disabledDate(time) {
          return time.getTime() < Date.now() - 24 * 60 * 60 * 1000;
        }
      },
      options,
      delExcel: true, // 
      isShowDate: false, // s
      telContent: "", // 
      userAdmin: [], //userInfo
      fileList: [], //
      //from
      ruleForm: {
        taskName: "", // 
        signId: null, // ID
        smsTemplates1: "", // 
        templateId: null, // ID
        notVariableMobile: "", //
        sendSign: "off", // 
        useId: 1,
        sendDt1: null,
        sendDt2: null,
        token: this.$cookies.get("token")
      },
      variableSign: 0, // 
      file: null, // 
      sendSign: false, // false
      radio: "1",
      file: null,
      searchSigns: {}, // 
      templateDatas: {}, // 
      searchSign: {
        page: 1, // 
        rows: 9999 // 
      },
      templateData: {
        templateName: "", // 
        templateType: "", // 
        page: "1",
        rows: 9999
      },
      rules: {
        taskName: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 30, message: " 1  30", trigger: "blur" }
        ],
        notVariableMobile: [
          { required: true, validator: Validate.validatePhone, trigger: "blur" }
        ],
        templateId: [
          { required: true, message: "", trigger: "change" }
        ],
        signId: [
          { required: true, message: "", trigger: "change" }
        ],
        sendDt1: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        sendDt2: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      }
    };
  },
  created() {
    this.getSignList(); // 
    this.TemplateList(); // 
  },
  mounted() {
    // id
    this.userAdmin = sessionStorage.getData("userInfo");
  },
  methods: {
    /**
     * 1.   
     * 2.  
     * */
    chooseModelChild(val) {
      const obj = this.templateDatas.filter(child => child.id === val)[0];
      console.log(obj, "obj");
      this.variableSign = obj.variableSign;
      if (this.variableSign) {
        this.chooseTelModel("2");
      } else {
        this.chooseTelModel("1");
      }
      if (this.telContent) this.$refs.uploadRef.handleRemove();
    },

    // 
    chooseTelModel(val) {
      this.radio = val;
    },

    //  
    handleDateTime(val) {
      this.ruleForm.sendDt1 = val;
      this.isShowDate = this.handleIsChooseTime(
        this.ruleForm.sendDt1,
        this.ruleForm.sendDt2
      );
    },

    // 
    handleDateTime2(val) {
      this.ruleForm.sendDt2 = val;
      this.isShowDate = this.handleIsChooseTime(
        this.ruleForm.sendDt1,
        this.ruleForm.sendDt2
      );
    },

    // 
    handleIsChooseTime(date, time) {
      if (date && time) {
        var timeVal = date + " " + time;
        var chooseTime = new Date(timeVal).getTime(); // 
        var nowTime = new Date().getTime(); // 
        if (chooseTime < nowTime) {
          return true;
        }
      }
      return false;
    },

    // switch
    handleSwitch(val) {
      if (val) {
        this.ruleForm.sendSign = "on";
      } else {
        this.ruleForm.sendSign = "off";
      }
    },

    // 
    handleTelTemplate() {
      SmsService.export(this.$cookies.get("token"));
    },

    // 
    handleGetFile(file) {
      this.file = file;
    },

    // excel
    handleExcelData(arr) {
      this.excelData = arr;
      this.telContent = "";
      if (arr.length > 20) {
        arr = arr.slice(0, 20);
      }
      arr.forEach(item => {
        this.telContent += item.tel + ",";
      });
      //  excel 
      // if (this.variableSign && this.excelData.length) {
      //   this.$message({
      //     type: "error",
      //     message: "<>!",
      //     duration: 5000
      //   });
      //   this.telContent = "";
      //   this.$refs.uploadRef.fileList = [];
      // }
    },

    // 
    chooseTemplate(val) {
      this.ruleForm.templateId = "";
      this.TemplateList(val);
    },

    // 
    async TemplateList(val) {
      this.templateData.templateName = this.ruleForm.templateId;
      this.templateData.templateType = val;
      const { code, data } = await SmsService.queryTemplateList(
        this.templateData
      );
      this.templateDatas = data.list;
    },

    // 
    async getSignList() {
      const { code, data } = await SmsService.signInfoList(this.searchSign);
      this.searchSigns = data.list;
    },

    // 
    async handleAddTask(obj) {
      const { data, code } = await SmsService.sendTask(obj);
      if (code == 200) {
        this.$message({
          type: "success",
          message: "!"
        });
        this.$router.push({ name: "sendTask" });
      } else {
        this.$message({
          type: "error",
          message: "!"
        });
      }
    },

    // 
    submitForm(formName, type) {
      this.$refs[formName].validate(valid => {
        if (valid && !this.isShowDate) {
          if (
            this.ruleForm.sendSign == "on" &&
            this.ruleForm.sendDt1 &&
            this.ruleForm.sendDt2
          ) {
            this.ruleForm.sendDt =
              this.ruleForm.sendDt1 + " " + this.ruleForm.sendDt2;
          }
          if (this.radio == "2") {
            this.ruleForm.notVariableMobile = "";
            if (!this.telContent) {
              this.$message({
                type: "warning",
                message: "!"
              });
              return;
            }
          }
          if (this.telContent) {
            let formData = new FormData();
            for (let key in this.ruleForm) {
              formData.append(key, this.ruleForm[key]);
            }
            formData.append("file", this.file);
            //  

            if (this.variableSign && type == "test") {
              this.$message({
                type: "warning",
                message: "${param}!"
              });
              return;
            } else {
              this.handleSendFile(formData);
            }
          } else {
            this.handleAddTask(this.ruleForm);
          }
        } else {
          return false;
        }
      });
    },

    // 
    handleSendFile(data) {
      let requestConfig = {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      };
      Axios.post("/push/send/addTask", data, requestConfig).then(res => {
        if (res.code == 200) {
          this.$message({
            message: "",
            type: "success"
          });
          this.$router.push({ name: "sendTask" });
        } else {
          this.$message({
            message: "",
            type: "error"
          });
        }
      });
    },

    // 
    async addMation() {
      if (this.fileList != "") {
        this.ruleForm.notVariableMobile = "";
      }
      if (this.ruleForm.notVariableMobile != "") {
        this.fileList = "";
      }
      let datas = {
        file: this.fileList, // 
        taskName: this.ruleForm.taskName, // 
        signId: this.ruleForm.signId, // ID
        templateId: this.ruleForm.templateId, // ID
        notVariableMobile: this.ruleForm.notVariableMobile, //
        sendDt: this.ruleForm.sendDt, // 
        sendSign: this.ruleForm.sendSign, // 
        useId: 113,
        token: this.$cookies.get("token")
      };
      const { code, data } = await SmsService.addTask(datas);
      if (code == 200) {
        this.$message({
          message: "",
          type: "success"
        });
      } else {
        this.$message({
          message: "",
          type: "error"
        });
      }
    },

    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style lang="less" scoped>
.tip-task .el-tooltip__popper {
  color: red !important;
}
.sms-wrapper {
  .model-btn {
    font-weight: 500;
    color: #909399;
    cursor: pointer;
  }
  .down-btn {
    color: #0cc2aa;
    cursor: pointer;
  }
  .tag-title {
    color: #0cc2aa;
  }
  .date-task {
    font-size: 12px;
    position: relative;
    top: -20px;
    color: #e55130;
  }
  .tel {
    margin: 10px 0;
  }
  .excel-content {
    margin-left: 120px;
  }
  .title {
    font-size: 18px;
    font-weight: 500;
  }
  .signle-title {
    height: 10px;
    margin-top: -8px;
    margin-left: 120px;
    color: #a9b0b4;
    font-size: 12px;
  }
  .task-title {
    color: #a9b0b4;
    font-size: 12px;
    padding: 0 15px;
    margin: 0;
    li {
      height: 15px;
      margin-bottom: 3px;
    }
  }
  .tel-content {
    border-radius: 4px;
    border: 1px solid #dcdfe6;
    font-size: 14px;
    line-height: 40px;
    padding: 0 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
    margin-bottom: 20px;
  }
  .tel-box {
    border-radius: 4px;
    border: 1px solid #dcdfe6;
    font-size: 12px;
    padding: 0 15px;
    cursor: pointer;
  }
  .tel-task {
    font-size: 12px;
    color: #606266;
    margin: 8px 0;
  }
}
</style>
