<template>
  <div>
    <el-upload
      ref="uploadRef"
      class="upload-demo"
      action
      :on-change="handleChange"
      :on-remove="handleRemove"
      :file-list="fileList"
      :limit="limitUpload"
      :auto-upload="false"
      :on-error="handleError"
      accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
    >
      <el-button size="small" type="info"></el-button>
      <div slot="tip" class="el-upload__tip">xlsx/xls </div>
    </el-upload>
  </div>
</template>

<script>
// accept 
export default {
  data() {
    return {
      limitUpload: 1, // 1
      fileTemp: null, // 
      fileListUpload: [], //
      sizeLimit: 3, // 3m
      formats: ["xlsx", "csv", "xls"],
      fileList: []
    };
  },
  created() {},
  mounted() {},
  methods: {
    /**
     * 
     *
     * */
    handleChange(file, fileList) {
      this.fileTemp = file.raw;
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" ||
          this.fileTemp.type == "application/vnd.ms-excel"
        ) {
          this.$emit("handleGetFile", this.fileTemp);
          this.importfile(this.fileTemp);
        } else {
          this.$message({
            type: "warning",
            message: ""
          });
        }
      } else {
        this.$message({
          type: "warning",
          message: ""
        });
      }
    },

    /**
     *  fileTemp null
     *
     * */
    handleRemove(file) {
      this.fileList = [];
      this.fileTemp = null;
      this.file = null;
      this.$emit("handleExcelData", []);
    },

    /**
     *  
     *
     * */
    importfile(obj) {
      let _this = this;
      // DOM
      this.file = obj;
      var rABS = false; //
      var f = this.file;
      var excleData = null;
      var reader = new FileReader();
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = "";
        var rABS = false; //
        var pt = this;
        var wb; //
        var outdata;
        var reader = new FileReader();
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result);
          var length = bytes.byteLength;
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          var XLSX = require("xlsx");
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //
              type: "base64"
            });
          } else {
            wb = XLSX.read(binary, {
              type: "binary"
            });
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]); //outdata
          this.da = [...outdata];
          let arr = [];
          this.da.map((v, i) => {
            let obj = {};
            obj.tel = v[""];
            obj.type = v["1"];
            arr.push(obj);
          });
          setTimeout(function() {
            _this.$emit("handleExcelData", arr);
          }, 200);
          return arr;
        };
        reader.readAsArrayBuffer(f);
      };

      if (rABS) {
        reader.readAsArrayBuffer(f);
      } else {
        reader.readAsBinaryString(f);
      }
    },

    // 
    handleError(err, file, fileList) {
      this.$message.error("");
    }
  }
};
</script>

<style lang="less" scoped>
.el-upload__tip {
  margin: 10px 0;
}
</style>